.footer {
  background-color: #0d0d0d;
  color: #fff;
  padding: 1.5rem 2rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #333;
}

.footer-left .logo {
  font-size: 1.2rem;
  font-weight: bold;
}

.footer-left .accent {
  color: gold;
  font-style: italic;
}

.footer-right {
  display: flex;
  align-items: center;
}

.footer-right img {
  width: 20px;
  height: 20px;
  margin-right: 0.5rem;
}

.footer-right a {
  color: #ccc;
  font-size: 1rem;
  text-decoration: none;
  transition: color 0.2s ease;
}

.footer-right a:hover {
  color: gold;
}
.gmail-icon {
  width: 20px;
  height: 20px;
  filter: invert(39%) sepia(93%) saturate(2896%) hue-rotate(340deg) brightness(99%) contrast(94%);
  margin-right: 8px;
  vertical-align: middle;
}

/* Í∏∞Ï°¥ PC Ïä§ÌÉÄÏùºÏùÄ Í∑∏ÎåÄÎ°ú Ïú†ÏßÄÎê® */

@media (max-width: 768px) {
  .footer {
    flex-direction: column;
    align-items: flex-start;
    gap: 0.75rem;
  }

  .footer-left {
    width: 100%;
  }

  .footer-right {
    flex-direction: row;
    flex-wrap: wrap;
    width: 100%;
    gap: 0.5rem;
  }

  .footer-left .logo {
    margin-bottom: 0.5rem;
  }

  .footer-right .gmail-icon {
    margin-left: 50px; /* Ïù¥Î¶ÑÍ≥º ÏïÑÏù¥ÏΩò ÏÇ¨Ïù¥ Ïó¨Î∞± */
  }
}

